// ç”¨æˆ·å­—æ®µé€‰æ‹©æŽ¥å£
export interface FieldSelection {
  dimensions: string[];  // ç”¨æˆ·é€‰æ‹©çš„ç»´åº¦å­—æ®µID
  measures: string[];    // ç”¨æˆ·é€‰æ‹©çš„æŒ‡æ ‡å­—æ®µID
  groupMeasure?: NestedMeasure<string>[]; // å¯é€‰çš„åˆ†ç»„æŒ‡æ ‡å­—æ®µID
  rowDimensions?: string[];  // è¡Œç»´åº¦å­—æ®µï¼ˆç”¨äºŽé€è§†è¡¨ç­‰ï¼‰
  columnDimensions?: string[];  // åˆ—ç»´åº¦å­—æ®µï¼ˆç”¨äºŽé€è§†è¡¨ç­‰ï¼‰
}

// Pipelineä¸Šä¸‹æ–‡æŽ¥å£
export interface PipelineContext {
  chartType: ChartType; // å›¾è¡¨ç±»åž‹
  encodes: ChannelMapping[]; // æ˜ å°„é€šé“é…ç½®
  fieldMap: FieldMap;        // å®Œæ•´å­—æ®µæ˜ å°„å®šä¹‰
  fieldSelection: FieldSelection;  // ç”¨æˆ·å­—æ®µé€‰æ‹©
  data: Record<string, any>[]; // æ•°æ®æ˜ å°„æ•°ç»„
  visualStyle?: any;
  
  // ç»´åº¦é‡å¡‘ç›¸å…³
  analysisResult?: any;       // å›¾è¡¨é€‚é…åˆ†æžç»“æžœ
  theme: 'light' | 'dark' | 'custom'; // ä¸»é¢˜é…ç½®
  version: string; // ç‰ˆæœ¬ä¿¡æ¯
  [key: string]: any;
}

// Pipelineç®¡é“å‡½æ•°ç±»åž‹ - ç®€åŒ–ä¸ºçº¯å‡½æ•°
export type PipelineStep<T = any> = (input: T, context: PipelineContext) => T;

// ç®€åŒ–çš„ç®¡é“å‡½æ•°å·¥åŽ‚ - ä½¿ç”¨reduceå®žçŽ°å‡½æ•°å¼ç»„åˆ
export const pipeline = <T>(steps: PipelineStep<T>[], initialValue: T) => {
  return (context: PipelineContext) => {
    return steps.reduce((result, step) => {
      return step(result, context);
    }, initialValue);
  };
};

