  BAR_PERCENT = 'bar_percent',     // ç™¾åˆ†æ¯”æŸ±çŠ¶å›¾
  
  COLUMN = 'column',               // æ¡å½¢å›¾
  COLUMN_STACKED = 'column_stacked', // å †å æ¡å½¢å›¾
  COLUMN_GROUPED = 'column_grouped', // åˆ†ç»„æ¡å½¢å›¾
