// åŒYè½´çº¿æ€§é…ç½®
export const yyLinearAxis: PipelineStep = (spec: any, context: PipelineContext) => {
  const result = { ...spec };
  if (!result.axes) {
    result.axes = [];
  }

  result.axes.push({
    orient: 'left',
    type: 'linear'
  });

  result.axes.push({
    orient: 'right',
    type: 'linear'
  });
  return result;
};