import { PipelineStep, PipelineContext } from '../../PipelineCore';

// VChartæ•£ç‚¹å›¾åˆå§‹åŒ–
export const initVChartScatter: PipelineStep = (spec: any, context: PipelineContext) => {
  const encodes = context.encodes[0] || {};
  
  return {
    ...spec,
    type: 'scatter',
    xField: encodes.x || 'x',
    yField: encodes.y || 'y',
    seriesField: encodes.color || encodes.group
  };
};